<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input
          placeholder="请输入搜索内容"
          v-model="input"
          class="input-with-select"
          @clear="ss"
          clearable
        >
          <el-button slot="append" icon="el-icon-search" @click="ss"></el-button>
        </el-input>
        <el-button type="primary" @click="addyh">添加商品</el-button>
      </div>
      <template>
        <el-table :data="list.goods" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="goods_name" label="商品名称" width="600"></el-table-column>
          <el-table-column prop="goods_price" label="商品价格（元）" width="160"></el-table-column>
          <el-table-column prop="goods_weight" label="商品重量" width="160"></el-table-column>
          <el-table-column prop="add_time" label="创建时间" width="160">
            <template slot-scope="scope">{{scope.row.add_time|time}}</template>
          </el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="list.total"
      ></el-pagination>
      </div>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      yi: "",
      er: "",
      lj: "",
      ym: "1",
      myxs: "10",
      input: "",
      list: [],
      sfxx: [],
    }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    handleSizeChange(val) {
      this.myxs = val;
      this.axios
        .get(this.lj + "?query=&pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ym = val;
      this.axios
        .get(this.lj + "?query=&pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    open(i) {
      this.$confirm("此操作将永久删除该商品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
            console.log(i)
          console.log(this.lj+"/" + i.goods_id);
          this.axios.delete(this.lj+"/" + i.goods_id).then(res => {
            console.log(res);
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.list = res.data.data;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    ss() {
        this.axios
        .get(
          this.lj +
            "?pagenum=" +
            this.ym +
            "&pagesize=" +
            this.myxs +
            "&query=" +
            this.input
        )
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    addyh(){
        this.$router.push("/about/add")
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.yi = sessionStorage.getItem("yi");
    this.lj = sessionStorage.getItem("ly");
    this.er = sessionStorage.getItem("er");
    this.axios.get(this.lj + "?query=&pagenum=1&pagesize=10").then(res => {
      this.list = res.data.data;
      console.log(this.list);
    });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.el-card__body {
  padding: 20px;
  background: #fff;
}
.el-button--primary {
  margin-left: 20px;
}
.el-input {
  width: 40% !important;
}
.bg {
  width: 100%;
}
.sou {
  margin-bottom: 20px;
}
.tda {
  width: 40px;
}
.el-pagination {
  margin-top: 15px;
}
.el-input-group {
  width: 40%;
}
.el-table {
  font-size: 12px;
}
</style>
