<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input placeholder="请输入内容" v-model="input" class="input-with-select" clearable disabled>
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      <template>
        <el-table :data="huser" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="order_number" label="订单编号" width="220"></el-table-column>
          <el-table-column prop="order_price" label="订单价格" width="145"></el-table-column>
          <el-table-column prop="pay_status" label="是否付款" width="145">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.pay_status==0" type="danger" effect="dark">未付款</el-tag>
              <el-tag v-if="scope.row.pay_status==1" effect="dark">已付款</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label="是否发货" width="145"></el-table-column>
          <el-table-column prop="update_time" label="下单时间" width="169">
            <template slot-scope="scope">{{scope.row.update_time|time}}</template>
          </el-table-column>
          <el-table-column prop="email" label="操作" width="200">
            <template slot-scope="scope">
              <el-tooltip placement="top" :enterable="false">
                <div slot="content">修改订单地址</div>
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="xgyhff(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip placement="top" :enterable="false">
                <div slot="content">物流信息</div>
                <el-button
                  type="success"
                  icon="el-icon-location"
                  size="mini"
                  @click="fp(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="fy.total"
      ></el-pagination>
    </div>
    <el-dialog title="修改地址" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="省市区/县" prop="region">
          <el-select v-model="ruleForms.regions" placeholder="请选择" @click="ssq()">
            <el-option label="区域一" value="shanghai"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="详细地址" prop="name">
          <el-input v-model="ruleForms.names"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false">取 消</el-button>
        <el-button type="primary" @click="xgyh()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="物流信息" :visible.sync="dialogFormVisibless">
      <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-steps direction="vertical" :active="1">
          <el-step v-for="(i,k) in sfxx" :key="k" :title="i.context"></el-step>
        </el-steps>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      yi: "",
      er: "",
      input: "",
      xxxx: "10",
      ssss: "1",
      huser: [],
      fy: [],
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: [],
      ruleForm: {
        name: "",
        pwd: "",
        yx: "",
        tel: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: "",
        regions: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 16, message: "长度在 3 到 16 个字符", trigger: "blur" }
        ],
        yx: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { min: 8, max: 25, message: "长度在 8 到 25 个字符", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "请输入电话", trigger: "blur" },
          { min: 3, max: 11, message: "长度在 3 到 11 个字符", trigger: "blur" }
        ]
      }
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    handleSizeChange(val) {
      this.xxxx = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
        .then(res => {
          //   console.log(res);
          this.huser = res.data.data.goods;
          this.fy = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ssss = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
        .then(res => {
          console.log(res);
          this.huser = res.data.data.goods;
          this.fy = res.data.data;
        });
    },
    async fp(i) {
      await this.axios.get("kuaidi/1106975712662").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
      this.dialogFormVisibless = true;
    },
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put("users/" + this.cid, {
          email: this.ruleForms.yxs,
          mobile: this.ruleForms.tels
        })
        .then(res => {
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    ssq(){

    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    this.axios
      .get("orders?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
      .then(res => {
        console.log(res);
        this.huser = res.data.data.goods;
        this.fy = res.data.data;
        console.log(this.huser);
      });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.el-card__body {
  padding: 20px;
  background: #fff;
}
.el-button--primary {
  margin-left: 20px;
}
.el-input {
  width: 40% !important;
}
.bg {
  width: 100%;
}
.sou {
  margin-bottom: 20px;
}
.tda {
  width: 40px;
}
.el-pagination {
  margin-top: 15px;
}
.el-input-group {
  width: 40%;
}
.p2 {
  font-size: 12px;
  color: #ccc;
}
.box {
  margin-top: 20px;
}
</style>
