<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <el-button type="primary" @click="dialogFormVisible = true">添加角色</el-button>
      <!-- <template>
        <el-table :data="list" stripe style="width: 100%" border>
          <el-table-column label width="60">
              <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
                @change="qqhh(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="roleName" label="角色名称" width="330"></el-table-column>
          <el-table-column prop="roleDesc" label="角色描述" width="330"></el-table-column>
          <el-table-column prop="email" label="操作" width="330">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)">编辑</el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)">删除</el-button>
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="fp(scope.row)">分配权限</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>-->
      <template>
        <el-table :data="list" style="width: 100%" border>
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-row
                :class="['bdbotton',k==0?'bdtop':'','jzdq']"
                v-for="(i,k) in scope.row.children"
                :key="i.id"
              >
                <el-col :span="5">
                  <el-tag closable @close="bdsc(scope.row,i.id)">{{i.authName}}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    :class="[kk===0?'':'bdtop','jzdq']"
                    v-for="(ii,kk) in i.children"
                    :key="ii.id"
                  >
                    <el-col :span="6">
                      <el-tag
                        type="success"
                        closable
                        @close="bdsc(scope.row,ii.id)"
                      >{{ii.authName}}</el-tag>
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        v-for="(iii) in ii.children"
                        :key="iii.id"
                        type="warning"
                        closable
                        @close="bdsc(scope.row,iii.id)"
                      >{{iii.authName}}</el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <el-table-column label="#" type="index" width="80"></el-table-column>
          <el-table-column label="角色名称" prop="roleName" width="325"></el-table-column>
          <el-table-column label="角色描述" prop="roleDesc" width="325"></el-table-column>
          <el-table-column label="操作" width="325">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="xgyhff(scope.row)"
              >编辑</el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)">删除</el-button>
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="fp(scope.row)"
              >分配权限</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <el-dialog title="添加角色" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="pwd">
          <el-input v-model="ruleForm.pwd"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="tjyh()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="修改角色" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="names">
          <el-input v-model="ruleForms.names"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="yxs">
          <el-input v-model="ruleForms.yxs"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false">取 消</el-button>
        <el-button type="primary" @click="xgyh()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="分配权限" :visible.sync="dialogFormVisibless" @close="qk">
      <el-tree
        :data="sfxx"
        :props="defaultProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="arr"
        ref="treeRef"
      ></el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibless = false">取 消</el-button>
        <el-button type="primary" @click="fen()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      arr: [],
      roId:'',
      yi: "",
      er: "",
      lj: "",
      list: [],
      dialogFormVisible: false,
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: [],
      ruleForm: {
        name: "",
        pwd: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入角色描述", trigger: "blur" },
          { min: 3, max: 16, message: "长度在 3 到 16 个字符", trigger: "blur" }
        ]
      },
      defaultProps: {
        children: "children",
        label: "authName"
      }
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.ruleForms.names = ki.roleName;
      this.ruleForms.yxs = ki.roleDesc;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put(this.lj + "/" + this.cid, {
          roleName: this.ruleForms.names,
          roleDesc: this.ruleForms.yxs
        })
        .then(res => {
          this.axios.get(this.lj).then(res => {
            console.log(res);
            this.list = res.data.data;
            console.log(this.list);
          });
        });
    },
    open(i) {
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          console.log(this.lj + "/" + i.id);
          this.axios.delete(this.lj + "/" + i.id).then(res => {
            // console.log(res);
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.axios.get(this.lj).then(res => {
            console.log(res);
            this.list = res.data.data;
            console.log(this.list);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    async fp(i) {
      this.roId=i.id;
      console.log(i);
      await this.axios.get("rights/tree").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
      this.qx(i, this.arr);
      this.dialogFormVisibless = true;
    },
    qk() {
      this.arr = [];
      location.reload();
    },
    fen() {
      console.log(this.arr)
      const keys=[
        ...this.$refs.treeRef.getCheckedKeys(),...this.$refs.treeRef.getHalfCheckedKeys(),
      ]
      const idStr=keys.join(",")
      this.axios.post("roles/"+this.roId+"/rights",{rids:idStr}).then((res)=>{
        console.log(res)
        this.dialogFormVisibless = false;
      })
    },
    qx(i, arr) {
      console.log(i)
      if (!i.children) {
        return this.arr.push(i.id);
      }
      i.children.forEach(item => {
        this.qx(item, this.arr);
      });
    },
    tjyh() {
      this.dialogFormVisible = false;
      this.axios
        .post(this.lj, {
          roleName: this.ruleForm.name,
          roleDesc: this.ruleForm.pwd
        })
        .then(res => {
          // console.log(res)
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.list = res.data.data;
            });
        });
    },
    async bdsc(aa, b) {
      console.log(aa, b);
      const a = await this.$confirm(
        "此操作将永久删除该权限, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (a != "confirm") {
        return this.$message.info("取消删除");
      }
      await this.axios.delete("roles/" + aa.id + "/rights/" + b).then(res => {
        aa.children = res.data.data;
      });
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    this.axios.get(this.lj).then(res => {
      console.log(res);
      this.list = res.data.data;
      console.log(this.list);
    });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.el-card__body {
  padding: 20px;
  background: #fff;
}
.el-table--border {
  margin-top: 20px;
}
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbotton {
  border-bottom: 1px solid #eee;
}
.jzdq {
  display: flex;
  align-items: center;
}
</style>
