<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input
          placeholder="请输入内容"
          v-model="input"
          class="input-with-select"
          @clear="ss"
          clearable
        >
          <el-button slot="append" icon="el-icon-search" @click="ss"></el-button>
        </el-input>
        <el-button type="primary" @click="dialogFormVisible = true">添加用户</el-button>
      </div>
      <template>
        <el-table :data="huser" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="username" label="姓名" width="160"></el-table-column>
          <el-table-column prop="email" label="邮箱" width="160"></el-table-column>
          <el-table-column prop="mobile" label="电话" width="160"></el-table-column>
          <el-table-column prop="role_name" label="角色" width="160"></el-table-column>
          <el-table-column prop="sg_state" label="状态" width="150">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
                @change="qqhh(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="email" label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="fp(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[2, 3, 4, 5]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="fy.total"
      ></el-pagination>
    </div>
    <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pwd">
          <el-input v-model="ruleForm.pwd"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="yx">
          <el-input v-model="ruleForm.yx"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="tel">
          <el-input v-model="ruleForm.tel"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="tjyh()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑用户" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="names">
          <el-input v-model="ruleForms.names" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="yxs">
          <el-input v-model="ruleForms.yxs"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="tels">
          <el-input v-model="ruleForms.tels"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false">取 消</el-button>
        <el-button type="primary" @click="xgyh()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="分配角色" :visible.sync="dialogFormVisibless">
      <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="当前用户">
          <el-input v-model="rule.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="当前角色">
          <el-input v-model="rule.js" disabled></el-input>
        </el-form-item>
        <el-form-item label="分配新角色">
          <el-select v-model="rule.xjs" placeholder="请选择身份">
            <el-option v-for="(i,k) in sfxx" :key="k" :label="i.roleName" :value="i.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibless = false">取 消</el-button>
        <el-button type="primary" @click="fen()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      lj: "",
      arr: [],
      yi: "",
      er: "",
      sms: "",
      huser: [],
      input: "",
      fy: [],
      sssj: [],
      myxs: "2",
      ym: "1",
      cid: "",
      tid: "",
      vid: "",
      dialogFormVisible: false,
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: "",
      ruleForm: {
        name: "",
        pwd: "",
        yx: "",
        tel: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 16, message: "长度在 3 到 16 个字符", trigger: "blur" }
        ],
        yx: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { min: 8, max: 25, message: "长度在 8 到 25 个字符", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "请输入电话", trigger: "blur" },
          { min: 3, max: 11, message: "长度在 3 到 11 个字符", trigger: "blur" }
        ]
      }
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    handleSizeChange(val) {
      this.myxs = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ym = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    open(i) {
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          console.log("roles/" + i.id);
          this.axios.delete("users/" + i.id).then(res => {
            // console.log(res);
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    tjyh() {
      this.dialogFormVisible = false;
      this.axios
        .post("users", {
          username: this.ruleForm.name,
          password: this.ruleForm.pwd,
          email: this.ruleForm.yx,
          mobile: this.ruleForm.tel
        })
        .then(res => {
          // console.log(res)
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.ruleForms.names = ki.username;
      this.ruleForms.yxs = ki.email;
      this.ruleForms.tels = ki.create_time;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put("users/" + this.cid, {
          email: this.ruleForms.yxs,
          mobile: this.ruleForms.tels
        })
        .then(res => {
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    ss() {
      this.axios
        .get(
          this.lj +
            "?pagenum=" +
            this.ym +
            "&pagesize=" +
            this.myxs +
            "&query=" +
            this.input
        )
        .then(res => {
          console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    qqhh(i) {
      this.axios.put("users/" + i.id + "/state/" + i.mg_state).then(res => {
        console.log(res);
      });
    },
    async fp(i) {
      console.log(i);
      this.dialogFormVisibless = true;
      this.rule.name = i.username;
      this.rule.js = i.role_name;
      this.tid = i.id;
      await this.axios.get("roles").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
    },
    fen() {
      this.axios
        .put("users/" + this.tid + "/role", { rid: this.rule.xjs })
        .then(res => {
          console.log(res);
          this.dialogFormVisibless = false;
        });
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  async mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    await this.axios
      .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
      .then(res => {
        console.log(res);
        this.huser = res.data.data.users;
        this.fy = res.data.data;
      });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.el-card__body {
  padding: 20px;
  background: #fff;
}
.el-button--primary {
  margin-left: 20px;
}
.el-input {
  width: 40% !important;
}
.bg {
  width: 100%;
}
.sou {
  margin-bottom: 20px;
}
.tda {
  width: 40px;
}
.el-pagination {
  margin-top: 15px;
}
.el-input-group {
  width: 40%;
}
</style>
