<template>
  <div class="boxx">
    <div class="box">
      <van-form>
        <van-field
          v-model="username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px;" id="footer">
          <el-button type="success" @click="onSubmit">登录</el-button>
          <el-button type="danger" @click="onCl">重置</el-button>
        </div>
      </van-form>
    </div>
    <div class="y">
      <img src="../assets/logo.png" alt />
    </div>
    <div class="ts"></div>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "home",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    onSubmit() {
      this.axios
        .post("login", { username: this.username, password: this.password })
        .then(res => {
          // console.log(res);
          if (res.data.meta.status == 200) {
            this.$toast("登录成功");
            sessionStorage.setItem("token",res.data.data.token)
            setTimeout(() => {
              this.$router.push("/About");
            }, 2000);
          }else{
            this.$toast(res.data.meta.msg);
          }
        });
    },
    onCl() {
      (this.username = ""), (this.password = "");
      this.$toast("重置成功");
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

