  width: 100%;
  height: 100%;
  background: #2b4b6b;
  position: relative;
}
.box {
  width: 500px;
  height: 300px;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-250px, -150px);
  border-radius: 5px;
}
.van-form {
  margin-top: 90px;
}
.y {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #fff;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 10%;
  border: 5px solid #ccc;
  transform: translate(-75px);
}
img {
  width: 155px;
}
#footer {
  display: flex;
  justify-content: space-around;
  padding: 20px;
}
